.item {
    display: grid;
    margin: 0 auto;

    grid-template-areas:
    "h h";

    grid-gap: 10px;

    padding: 10px;
}

.item img {
    width: 50px;
    height: 50px;
    border-radius: 30px;
}

.avatar {
    align-items: left;
    margin: 0 auto;
}

.nickname {
    margin:10px auto 5px;
}